body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to left, rgb(48, 110, 121) , rgb(83, 182, 160));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.crd{
    cursor: pointer;
    background-image: linear-gradient(to bottom right,rgba(119, 151, 180, 0.897),rgb(176, 241, 224));
}
h2{
    font-family: cursive;
}
img{
    width: 200px;
    height: 200px;
}
